/* Pop Up */
/* code help from W3 Schools */
.modal-style {
    position: fixed;
    background-color: var(--secondary-background-color);
    border: 2px solid var(--borders-color);
    padding: 10px;
    z-index: 1;
    width: 90%;
    height: 94%;
    overflow: auto;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); /* help from mdn web docs */
  }
  
  .modal-style h2 {
    color: var(--primary-font-color);
  }
  
  .modal-box-content-mainparagraph {
    padding-right: 10px;
  }
  
  .modal-box-content p {
    padding-right: 10px;
  }
  
  .modal-box-content img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    height: 100%;
    border: 2px solid var(--borders-color);
  }
  
  .modal-box-content .f-container img {
    flex: 1;
    max-height: 400px;
    max-width: 400px;
  }
  
  .modal-box-content .f-container div {
    flex: 2;
    padding-right: 10px;
  }
  
  .modal-style h2 {
    font-size: 24px;
  }
  
  .modal-style p {
    font-size: 20px;
  }

  .hide {
    display: none;
  }