.page-paragraph-content {
  padding-top: 10px;
  padding-bottom: 10px;
}

.page-paragraph-box, page-list-box {
  padding: 10px;
}

.page-paragraph-box h2 {
  color:var(--primary-font-color);
}

.page-paragraph-box p {
  font-size: 18px;
}

/* Main page Specific */

.image-slider {
  border: 2px solid var(--borders-color);
}

.image-slider img {
  display: block;
  margin: 0 auto;
  width: 100%;
  max-height: 100%;
  object-fit: fill;
}

/* Larger Resolutions */
@media (min-width: 768px) {
  .page-paragraph-box {
    flex: 60%;
  }
  
  .four-img-stack {
    display: flex;
    justify-content: space-between;
  }
}